body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #f2f2f2;
  font-family: 'Roboto', sans-serif; }

.container {
  margin: 0.5rem; }

.profile-header {
  background-color: white;
  box-shadow: 1px 1px 7px 3px #bfbfbf;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: left;
          justify-content: left; }
  .profile-header > .profile-header__image {
    border-radius: 50%;
    width: 5rem;
    height: 5rem;
    margin: 1rem; }
  .profile-header > .profile-header__title {
    margin: auto;
    min-width: 14rem; }
    .profile-header > .profile-header__title > .profile-header__title--name {
      font-weight: bold;
      font-size: 1.2rem; }
    .profile-header > .profile-header__title > .profile-header__title--job {
      color: #DCDCDC;
      font-size: 0.9rem; }
    .profile-header > .profile-header__title > .profile-header__title--media > svg {
      margin: .4rem; }

.profile-body {
  background-color: white;
  box-shadow: 1px 1px 7px 3px #bfbfbf;
  margin-top: 1rem;
  padding: 1rem; }
  .profile-body > .profile-body__title {
    text-transform: uppercase;
    font-size: 1.1rem; }
  .profile-body > .profile-body__content {
    text-align: justify;
    font-size: 1rem;
    margin-top: .5rem; }

